@charset "UTF-8";
/* Сonnecting fonts */
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");

/* Box sizing rules */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Remove default margin */
* {
    margin: 0;
    padding: 0;
    font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
    list-style: none;
}

/* Set core root defaults */
html:focus-within {
    scroll-behavior: smooth;
}

html,
body {
    height: 100%;
}

/* Set core body defaults */
body {
    text-rendering: optimizeSpeed;
    line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
    text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
    max-width: 100%;
    display: block;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
    html:focus-within {
        scroll-behavior: auto;
    }

    *,
    *::before,
    *::after {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

/* Base Styles */
body {
    font-size: 16px;
    font-weight: 400;
    font-family: "Montserrat", "Segoe UI", Arial, sans-serif;
    background: #ffffff;
    color: #383838;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.main {
    flex: 1 1 auto;
}

.container {
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
    width: min(100%, 1920px);
}

@media only screen and (min-width: 1281px) {
    .container {
        padding-left: 80px;
        padding-right: 80px;
    }
}

@media only screen and (min-width: 1601px) {
    .container {
        padding-left: 130px;
        padding-right: 130px;
    }
}

/* UI elements "Navs & Tabs" */
.primary-menu__button {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    justify-content: space-between;
    width: 32px;
    height: 22px;
    margin: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

@media only screen and (min-width: 769px) {
    .primary-menu__button {
        display: none;
    }
}

.primary-menu__button span {
    height: 3px;
    border-radius: 1000px;
    background-color: #383838;
}

.primary-menu__button span:nth-child(1) {
    width: 24px;
}

.primary-menu__button span:nth-child(2) {
    width: 32px;
}

.primary-menu__button span:nth-child(3) {
    width: 18px;
}

.primary-menu__list {
    display: flex;
    align-items: center;
    list-style-type: none;
}

.primary-menu__list>*+* {
    margin: 0 0 0 22px;
}

.primary-menu__list-item {
    padding: 4px 0 4px 0;
}

.primary-menu__list-link {
    color: #171717;
    font-size: 16px;
    line-height: 20px;
    text-decoration: none;
}

.secondary-menu {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.secondary-menu__item {
    flex: 0 0 auto;
}

.secondary-menu__item-title {
    font-size: 18px;
    line-height: 22px;
}

.secondary-menu__list {
    list-style-type: none;
}

.secondary-menu__list-item {
    margin: 10px 0 0 0;
}

.secondary-menu__list-link {
    opacity: 0.5;
    font-size: 16px;
    line-height: 20px;
    text-decoration: none;
}

/* UI elements "Covers" */
/* UI elements "Cards" */
/* UI elements "Links" */
.logo {
    display: flex;
    align-items: center;
}

.logo__link {
    text-decoration: none;
}

.logo__img {
    width: 100%;
}

.color-palette {
    display: flex;
    align-items: center;
    color: #787d46;
    font-size: 16px;
    line-height: 20px;
    text-decoration: none;
}

.color-palette__icon {
    width: 24px;
    height: 24px;
}

.color-palette__label {
    margin: 0 0 0 12px;
    color: inherit;
}

.contact-email,
.contact-phone {
    color: inherit;
    font-size: 16px;
    line-height: 20px;
    text-decoration: none;
}

.account-link,
.shopping-cart-link {
    text-decoration: none;
}

.account-link svg,
.shopping-cart-link svg {
    width: 24px;
    height: 24px;
}

.social-list {
    display: flex;
    align-items: center;
}

.social-list>*+* {
    margin: 0 0 0 16px;
}

.social-list__link {
    width: 24px;
    height: 24px;
}

/* UI elements "Forms" */
/* UI elements "Buttons" */
.button {
    border: none;
    border-radius: 999px;
    padding: 1.25em 2em 1.25em 2em;
    background-color: #787d46;
    cursor: pointer;
    /* typography*/
    color: #ffffff;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.button_fw-700 {
    font-weight: 700;
}

