/* UI elements "Carusels" */
/* Block "Header" */
.header__inner {
    padding: 20px 0 20px 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(56, 56, 56, 0.3);
}

.header__logo {
    width: 196px;
    height: 58px;
}

.header__menu {
    flex: 1 1 auto;
    padding: 0 32px 0 32px;
}

.header__contact {
    margin: 0 0 0 32px;
}

.header__account-and-shopping-cart {
    display: flex;
    margin: 0 0 0 50px;
}

.header__account-and-shopping-cart>*+* {
    margin: 0 0 0 18px;
}

/* Block "hero" */
.hero {
    display: flex;
    align-items: center;
    height: 800px;
    margin: 0 0 60px 0;
    background-image: url("../images/bg/bg-hero.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.hero__inner {
    max-width: 814px;
}

.hero__inner>*+* {
    margin: 32px 0 0 0;
}

.hero__title {
    color: #ffffff;
    font-size: 100px;
    line-height: 98px;
}

.hero__descr {
    color: #ffffff;
    font-size: 18px;
    line-height: 24px;
}

.hero__button {
    width: 391px;
}

/* Block "decription" */
.decription {
    min-height: 296px;
    padding: 60px 0 60px 0;
    background-image: url("../images/bg/bg-decor-waves.svg");
    background-repeat: repeat-x;
    background-position: center;
    background-size: auto 296px;
}

.decription__inner {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.decription__inner>*+* {
    margin: 30px 0 0 0;
}

.decription__text {
    max-width: 1400px;
    font-size: 32px;
    line-height: 46px;
    text-align: center;
}

.decription__button {
    width: 313px;
}

/* Block "Footer" */
.footer {
    padding: 60px 0 50px 0;
}

.footer__inner {
    padding: 40px 0 0 0;
    border-top: 1px solid rgba(56, 56, 56, 0.3);
}

.footer__logo {
    width: 196px;
    height: 58px;
    margin: 0 auto 0 auto;
}

.footer__menu {
    margin: 60px 0 0 0;
}

.footer__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 40px 0 0 0;
}

.footer__copyrigth {
    display: flex;
    align-items: center;
}

.footer__copyrigth-text {
    font-size: 16px;
    line-height: 20px;
}

.footer__copyrigth-links {
    padding: 0 30px 0 30px;
}

.footer__copyrigth-links>*+* {
    margin: 0 0 0 22px;
}

.footer__copyrigth-link {
    color: inherit;
    opacity: 0.5;
    font-size: 16px;
    line-height: 20px;
    text-decoration-line: underline;
}

.footer__contact-and-social {
    display: flex;
    align-items: center;
}

.footer__contact>*+* {
    margin: 0 0 0 22px;
}

.footer__social {
    margin: 0 0 0 40px;
}